    import uploadImages from "../../components/common/upload-images.vue"
    import uniNavBar from "../../components/uni-nav-bar/uni-nav-bar.vue";
    import uniPopup from '../../components/uni-popup/uni-popup.vue'
	export default {
        components:{
            uniNavBar,
            uploadImages,
            uniPopup
        },
		data() {
			return {
                isTips2Save:false,
                showpop_up:true,
                title:"",
				visiblity:"综合1",
                text:"",//把text绑定到表单中
                imglist:[]
			}
		},
		methods: {
            //保存草稿
            saveAsDraft(){
                uni.showModal({
                    content: '是否保存为草稿',
                    cancelText: '不保存',
                    confirmText: "保存",
                    success: res => {
                        if(res.confirm){
                            console.log("save");
                            // this.text = 
                        }
                        else{
                             console.log("dont save");
                        }
                        this.isTips2Save = true;
                        uni.navigateBack(
                            {
                                delta:1
                            }
                        );
                    },
                    fail: () => {},
                    complete: () => {}
                });
               
            },
                
            upload(arr){
                this.imglist = arr;
            },
            //返回
			back(){
                uni.navigateBack({
                    delta:1000,
                })
            },
            //发布
            submit(){
                alert("发布")
            },
            changeVisibility(){
                uni.showActionSheet({
                    itemList:["时间线","综合1","速报2","欢乐恶搞","跑团","圈内"],
                    success:(res)=> {
                        switch (res.tapIndex){
                            case 0:
                                this.visiblity = "时间线";
                                break; 
                            case 1:
                                this.visiblity = "综合1";
                                break;
                            case 2:
                                this.visiblity = "速报2";
                                break;
                            case 3:
                                this.visiblity = "欢乐恶搞";
                                break;
                            case 4:
                                this.visiblity = "跑团";
                                break;
                            case 5:
                                this.visiblity = "圈内";
                                break;
                            
                        }
                    },
                    
                })
            },
            hidePopup(){
                this.showpop_up = false;
            }

		},
        onBackPress() {
            //如果有信息
            if(this.text.length == 0 && this.imglist.length == 0){return;}
            if(!this.isTips2Save){
                this.saveAsDraft();
                return true;
            }            
        },
	}
